import { createI18n } from 'vue-i18n-composable'
import localMsgs from './locales'

export const startLocale = process.env.VUE_APP_LNG ? process.env.VUE_APP_LNG : 'ru'

export const i18n = createI18n({
  locale: startLocale,
  messages: localMsgs,
  pluralizationRules: {
    'ru': function(choice = 0) {
      return choice
    },
    'en': function(choice = 0) {
      return choice
    },
  }
})
