import { mapActions, mapMutations, mapState } from 'vuex'
import { defineComponent } from '@vue/composition-api'
import FixFluidColGrid from '@/components/fix-fluid-col-grid/fix-fluid-col-grid.vue'
import Header from '@/components/header/header.vue'
import Loader from '@/components/loader/loader.vue'
import ConfirmAlertModal from '@/components/confirm-alert-modal/confirm-alert-modal.vue'
import Stub from '@/components/stub/stub.vue'

export default defineComponent({
  name: 'App',
  components: {
    FixFluidColGrid,
    VHeader: Header,
    Loader,
    ConfirmAlertModal,
    Stub
  },
  computed: {
    ...mapState({
      globalLoading: state => state.globalLoading,
      updateLoading: state => state.update.loading,
      errorConnection: state => state.errorConnection,
      serverError: state => state.serverError
    })
  },
  mounted() {
    this.checkJob()
  },
  methods: {
    ...mapActions({
      checkJob: 'update/checkJob'
    }),
    ...mapMutations({
      setServerError: 'SET_SERVER_ERROR'
    })
  },
  watch: {
    updateLoading() {
      if (!this.updateLoading) {
        this.$refs['refresh-alert-modal'].show()
      }
    },
    serverError() {
      if(this.serverError) {
        this.$refs['server-error-modal'].show()
      }
    }
  }
})