import { defineComponent } from '@vue/composition-api'
import { Line, mixins } from 'vue-chartjs'
import { useI18n } from 'vue-i18n-composable'
import FixFluidColGrid from '@/components/fix-fluid-col-grid/fix-fluid-col-grid.vue'


const Chart = {
  extends: Line,
  mixins: [mixins.reactiveProp],
  props: ['options'],
  mounted () {
    this.renderChart(this.chartData, this.options)
  }
}

function randomInteger(min, max) {
  let rand = min + Math.random() * (max + 1 - min);
  return Math.floor(rand);
}

export default defineComponent({
  name: 'StatisticChart',
  components: {
    Chart,
    FixFluidColGrid
  },
  setup() {
    const i18n = useI18n()
    const chartOptions = {
      tooltips: {
        enabled: true,
        callbacks: {
          title(tooltipItem, data) {
            const item = data.datasets[0].data[tooltipItem[0].index]
            const { hours, minutes, seconds } = item
            return i18n.t('chart.tootip', { hours, minutes, seconds })
          },
          label() {
            return ''
          }
        },
        backgroundColor: '#384667',
        titleFontSize: 14,
        titleFontStyle: 'normal',
        titleMarginBottom: 0
      },
      legend: {
        display: false
      },
      scales: {
        xAxes: [{
          type: 'time',
          time: {
            unit: 'day',
            displayFormats: {
              day: 'DD.MM'
            }
          },
          ticks: {
            fontSize: 14,
            fontColor: '#98A2BD',
            source: 'data'
          },
          gridLines: {
            zeroLineColor: '#FFFFFF',
            color: 'rgba(91,102,131, .2)'
          }
        }],
        yAxes: [{
          ticks: {
            fontSize: 14,
            fontColor: '#98A2BD',
            beginAtZero: true
          },
          gridLines: {
            zeroLineColor: '#FFFFFF',
            color: 'rgba(91,102,131, .2)'
          }
        }]
      },
      responsive: true,
      maintainAspectRatio: false
    }

    return {
      chartOptions
    }
  },
  data() {
    /**
     * Тестовые данные
     */
    const times = [ this.$moment() ]
    for (let i = 1; i < 10; i++) {
      const addedDate = this.$moment(times[i - 1]).subtract(1, 'days')
      times.push(addedDate)
    }
    /**
     * Конец тестовые данные
     */
    times.reverse()
    const chartData = times.map(t => {
      const value = randomInteger(0, 86400)
      const hours = Math.floor(value / 3600)
      const minutes =  Math.floor((value - hours * 3600) / 60)
      const seconds = Math.floor((value - hours * 3600 - minutes * 60) / 60)
      return {
        x: this.$moment(t).format(),
        y: hours,
        hours,
        minutes,
        seconds
      }
    })
    console.log('chartData', chartData)
    return {
      chartData
    }
  }
})