import { defineComponent } from '@vue/composition-api'
import { ValidationObserver, ValidationProvider } from 'vee-validate'
import LogoLogin from '@/assets/svg/logo-login.svg?inline'

export default defineComponent({
  name: 'Auth',
  components: {
    ValidationObserver,
    ValidationProvider,
    LogoLogin
  },
  methods: {
    onSubmit() {
      this.$refs.authForm.validate().then(result => {
        console.log('result', result)
      })
    }
  }
})